import { router } from '../router'
import wechath5 from './wechath5'
import { handleClientEvent } from './tools'
import Cache from './cache'

// 
export const wxpay = async (options) => {
    await handleClientEvent({
        // 
        MP_WEIXIN: () => {
            return new Promise((resolve, reject) => {
                uni.requestPayment({
                    provider: 'wxpay',
                    timeStamp: options.timeStamp,
                    // jssdktimestamptimeStampS
                    nonceStr: options.nonceStr,
                    //  32 
                    package: options.package,
                    // prepay_idprepay_id=***
                    signType: options.signType,
                    // 'SHA1''MD5'
                    paySign: options.paySign,

                    success: (res) => resolve(res),
                    cancel: (res) => reject(res),
                    fail: (res) => reject(res)
                })
            })
        },

        // 
        OA_WEIXIN: () => {
            return wechath5.wxPay(options)
        },

        // H5
        H5: () => {
            return new Promise((resolve, reject) => {
                window.open(options, '_self')
                resolve()
            })
        },

        // APP
        IOS: () => {
            return new Promise((resolve, reject) => {
                uni.requestPayment({
                    provider: 'wxpay',
                    orderInfo: options,
                    success: (res) => resolve(res),
                    cancel: (res) => reject(res),
                    fail: (res) => reject(res)
                })
            })
        },
        ANDROID: () => {
            return new Promise((resolve, reject) => {
                uni.requestPayment({
                    provider: 'wxpay',
                    orderInfo: options,
                    success: (res) => resolve(res),
                    cancel: (res) => reject(res),
                    fail: (res) => reject(res)
                })
            })
        }
    })
}

// 
export const alipay = async (options, params, token) => {
    await handleClientEvent({
        // 
        MP_WEIXIN: () => {
            return new Promise((resolve, reject) => {
                uni.$emit('Alipay')
                // resolve()
            })
        },
        // 
        OA_WEIXIN: () => {
            return new Promise((resolve, reject) => {
                uni.navigateTo({
                    url: `/bundle/pages/toAlipay/toAlipay?id=${params.order_id}&from=${params.from}&pay_way=${params.pay_way}&key=${token}`
                })
                resolve()
            })
        },
        // H5
        H5: () => {
            return new Promise((resolve, reject) => {
                const alipayPage = window.open('', '_self')
                alipayPage.document.body.innerHTML = options
                alipayPage.document.forms[0].submit()
                resolve()
            })
        },

        // APP
        IOS: () => {
            return new Promise((resolve, reject) => {
                uni.requestPayment({
                    provider: 'alipay',
                    orderInfo: options,
                    success: (res) => resolve('success'),
                    cancel: (res) => reject('fail'),
                    fail: (res) => reject('fail')
                })
            })
        },
        ANDROID: () => {
            console.log(options)
            return new Promise((resolve, reject) => {
                uni.requestPayment({
                    provider: 'alipay',
                    orderInfo: options,
                    success: (res) => resolve('success'),
                    cancel: (res) => reject('fail'),
                    fail: (res) => reject('fail')
                })
            })
        }
    })
}
export const ttpay = async (options) => {
    return new Promise((resolve, reject) => {
        tt.pay({
            orderInfo: options,
            service: 5,
            success: (res) => {
                if (res.code == 0) {
                    resolve(res)
                } else {
                    reject(res)
                }
            },
            cancel: (res) => reject(res),
            fail: (res) => reject(res)
        })
    })
}
